import React from "react";
import { injectIntl } from "react-intl";
import key from "../../../util/key";

import { Space, Tag } from "antd";
import { SearchOutlined } from "@ant-design/icons";
import DataTable from "../../../components/Datatable/DataTable";
import DataTablePeriod from "../../../components/Datatable/DataTablePeriod";
import DataTableSearch from "../../../components/Datatable/DataTableSearch";

class AdminDashboardSessions extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      tableUuid: key(),
      columns: [],
    };
  }

  componentDidMount() {
    this.setColumns();
  }

  componentDidUpdate(prevProps) {
    if (prevProps.intl.locale !== this.props.intl.locale) {
      this.setColumns();
    }
  }

  setColumns = () => {
    this.setState({
      columns: [
        {
          title: this.m("created"),
          dataIndex: "created",
          sorter: true,
          render: (value) => {
            if (value) {
              const dt = new Date(value);
              return (
                <>
                  {dt.toLocaleDateString()}
                  <br />
                  {dt.toLocaleTimeString()}
                </>
              );
            }
          },
          width: "10%",
          date: true,
          filterDropdown: (props) => <DataTablePeriod {...props} />,
        },
        {
          title: this.m("host"),
          dataIndex: "host",
          sorter: true,
          render: (value) => <>{value}</>,
          width: "10%",
        },
        {
          text: true,
          title: this.m("username"),
          dataIndex: "username",
          sorter: true,
          render: (value) => (
            <div style={{ whiteSpace: "nowrap" }}>{value}</div>
          ),
          filterDropdown: (props) => <DataTableSearch {...props} />,
          filterIcon: (filtered) => (
            <SearchOutlined
              style={{ color: filtered ? "#1890ff" : undefined }}
            />
          ),
        },
        {
          title: this.m("status"),
          dataIndex: "status",
          sorter: true,
          width: "10%",
          render: (value, item) => (
            <>
              <Tag>{item.statetext}</Tag> <Tag>{item.statustext}</Tag>
            </>
          ),
        },
      ],
    });
  };

  m = (key) => {
    const { intl } = this.props;
    return intl.formatMessage({ id: "admin.dashboard.sessions." + key });
  };

  render() {
    return (
      <Space direction="vertical">
        <h3>{this.m("sessions")}</h3>
        <DataTable
          uuid={this.state.tableUuid}
          columns={this.state.columns}
          path={"/admin/session/list"}
        />
      </Space>
    );
  }
}

export default injectIntl(AdminDashboardSessions);
